import Vue from 'vue'
import Router from 'vue-router'

import home from '@/views/home'
import login from '@/views/login';
import mains from '@/views/mains.vue'
// 基础资料-幼儿园管理-学校信息
import multimediAdd from '@/views/multimediaManagement/multimediAdd'
Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [{
    path: '/home',
    redirect: '/home',
    name: 'mains',
    component: mains,
    meta: {
      // title: '首页',
      requireAuth: true
    },
    children: [{
      path: '/home',
      name: 'home',
      component: home,
      meta: {
        requireAuth: true,
        title: '数据看板'
      }
    },
    //系统设置
    {
      path: '/resourceManagement',
      name: 'resourceManagement',
      component: () => import('@/views/systemSettings/resourceManagement'),
      meta: {
        requireAuth: true,
        title: '资源管理'
      }
    },

    {
      path: '/roleManagement',
      name: 'roleManagement',
      component: () => import('@/views/systemSettings/roleManagement'),
      meta: {
        requireAuth: true,
        title: '角色管理'
      }
    },
    {
      path: '/cacheManagement',
      name: 'cacheManagement',
      component: () => import('@/views/systemSettings/cacheManagement'),
      meta: {
        requireAuth: true,
        title: '缓存管理'
      }
    },
    {
      path: '/regionalManagement',
      name: 'regionalManagement',
      component: () => import('@/views/systemSettings/regionalManagement'),
      meta: {
        requireAuth: true,
        title: '区域管理'
      }
    },
    {
      path: '/dataDictionary',
      name: 'dataDictionary',
      component: () => import('@/views/systemSettings/dataDictionary'),
      meta: {
        requireAuth: true,
        title: '数据字典'
      }
    },
    {
      path: '/configurationManagement',
      name: 'configurationManagement',
      component: () => import('@/views/systemSettings/configurationManagement'),
      meta: {
        requireAuth: true,
        title: '运行参数'
      }
    },
    {
      path: '/resourceOrgManagement',
      name: 'resourceOrgManagement',
      component: () => import('@/views/systemSettings/resourceOrgManagement'),
      meta: {
        requireAuth: true,
        title: '机构资源'
      }
    },
    {
      path: '/roleOrgManagement',
      name: 'roleOrgManagement',
      component: () => import('@/views/systemSettings/roleOrgManagement'),
      meta: {
        requireAuth: true,
        title: '机构角色'
      }
    },
    {
      path: '/locationManagement',
      name: 'locationManagement',
      component: () => import('@/views/systemSettings/locationManagement'),
      meta: {
        requireAuth: true,
        title: '设备位置管理'
      }
    },
    //日志查询
    {
      path: '/loginLog',
      name: 'loginLog',
      component: () => import('@/views/journalSearch/loginLog'),
      meta: {
        requireAuth: true,
        title: '登录日志'
      }
    },
    {
      path: '/operationLog',
      name: 'operationLog',
      component: () => import('@/views/journalSearch/operationLog'),
      meta: {
        requireAuth: true,
        title: '操作日志'
      }
    },
    //账号管理

    {
      path: '/adminAccount',
      name: 'adminAccount',
      component: () => import('@/views/accountManagement/adminAccount'),
      meta: {
        requireAuth: true,
        title: '系统账号'
      }
    },
    {
      path: '/cityAccount',
      name: 'cityAccount',
      component: () => import('@/views/accountManagement/cityAccount'),
      meta: {
        requireAuth: true,
        title: '市级账号'
      }
    },
    {
      path: '/platManage',
      name: 'platManage',
      component: () => import('@/views/accountManagement/platManage'),
      meta: {
        requireAuth: true,
        title: '管理员账号'
      }
    },
    {
      path: '/areaAccount',
      name: 'areaAccount',
      component: () => import('@/views/accountManagement/areaAccount'),
      meta: {
        requireAuth: true,
        title: '区级账号'
      }
    },
    {
      path: '/schoolAccount',
      name: 'schoolAccount',
      component: () => import('@/views/accountManagement/schoolAccount'),
      meta: {
        requireAuth: true,
        title: '学校账号'
      }
    },
    {
      path: '/schoolManage',
      name: 'schoolManage',
      component: () => import('@/views/accountManagement/schoolManage'),
      meta: {
        requireAuth: true,
        title: '学校管理员'
      }
    },
    {
      path: '/userList',
      name: 'userList',
      component: () => import('@/views/systemSettings/userList'),
      meta: {
        requireAuth: true,
        title: '用户列表'
      }
    },
    {
      path: '/multimediaManagement',
      name: 'multimediaManagement',
      component: () => import('@/views/multimediaManagement/multimediaManagement'),
      meta: {
        requireAuth: true,
        title: '多媒体管理'
      }
    },
    {
      path: '/multimediAdd', //基础资料-幼儿园管理-学校信息
      name: 'multimediAdd',
      component: multimediAdd,
      meta: {
        requireAuth: true,
        title: '多媒体播放'
      },
    },
    // 新增-演练管理
    {
      path: '/drillManagement',
      name: 'drillManagement',
      component: () => import('@/views/drillManagement/drillManagement'),
      meta: {
        requireAuth: true,
        title: '演练管理'
      }
    },
    //演练分配
    {
      path: '/drillManAdd',
      name: 'drillManAdd',
      component: () => import('@/views/drillManagement/drillManAdd'),
      meta: {
        requireAuth: true,
        title: '演练分配'
      }
    },
    //新增铃声配置
    {
      path: '/ringtoneConfigurationMusic',
      name: 'ringtoneConfigurationMusic',
      component: () => import('@/views/drillManagement/component/ringtoneConfigurationMusic'),
      meta: {
        requireAuth: true,
        title: '新增铃声配置'
      }
    },
    {
      path: '/ringtoneConfiguration',
      name: 'ringtoneConfiguration',
      component: () => import('@/views/drillManagement/component/ringtoneConfiguration'),
      meta: {
        requireAuth: true,
        title: '铃声配置'
      }
    },
    //新增班牌配置
    {
      path: '/deckArrangement',
      name: 'deckArrangement',
      component: () => import('@/views/drillManagement/component/deckArrangement'),
      meta: {
        requireAuth: true,
        title: '班牌配置'
      }
    },
    {
      path: '/deckArrangementAdd',
      name: 'deckArrangementAdd',
      component: () => import('@/views/drillManagement/component/deckArrangementAdd'),
      meta: {
        requireAuth: true,
        title: '新增班牌配置'
      }
    },
    //新增屏幕配置
    {
      path: '/screenConfiguration',
      name: 'screenConfiguration',
      component: () => import('@/views/drillManagement/component/screenConfiguration'),
      meta: {
        requireAuth: true,
        title: '屏幕配置'
      }
    },
    {
      path: '/screenConfigurationAdd',
      name: 'screenConfigurationAdd',
      component: () => import('@/views/drillManagement/component/screenConfigurationAdd'),
      meta: {
        requireAuth: true,
        title: '新增屏幕配置'
      }
    },
    //新增预警灯配置
    {
      path: '/earlyWarningLightAdd',
      name: 'earlyWarningLightAdd',
      component: () => import('@/views/drillManagement/component/earlyWarningLightAdd'),
      meta: {
        requireAuth: true,
        title: '新增预警灯配置'
      }
    },

    {
      path: '/equipmentManagement',
      name: 'equipmentManagement',
      component: () => import('@/views/equipmentManagement/equipmentManagement'),
      meta: {
        requireAuth: true,
        title: '设备管理'
      }
    },
    {
      path: '/earlyWarning',
      name: 'earlyWarning',
      component: () => import('@/views/earlyWarning/earlyWarning'),
      meta: {
        requireAuth: true,
        title: '安全预警看板'
      }
    },
    {
      path: '/materialCenter',
      name: 'materialCenter',
      component: () => import('@/views/materialCenter/materialCenter'),
      meta: {
        requireAuth: true,
        title: '素材中心'
      }
    },
    {
      path: '/temporaryRingtone',
      name: 'temporaryRingtone',
      component: () => import('@/views/temporaryRingtone/temporaryRingtone'),
      meta: {
        requireAuth: true,
        title: '临时铃声'
      }
    },
    {
      path: '/drillResult',
      name: 'drillResult',
      component: () => import('@/views/drillResult/drillResult'),
      meta: {
        requireAuth: true,
        title: '演练结果'
      }
    },
    {
      path: '/equipmentSwitch',
      name: 'equipmentSwitch',
      component: () => import('@/views/equipmentSwitch/equipmentSwitch'),
      meta: {
        requireAuth: true,
        title: '设备开关'
      }
    },
    {
      path: '/warningProcessing',
      name: 'warningProcessing',
      component: () => import('@/views/warningProcessing/warningProcessing'),
      meta: {
        requireAuth: true,
        title: '病假预警处理'
      }
    },
    {
      path: '/studentManagement',
      name: 'studentManagement',
      component: () => import('@/views/studentManagement/studentManagement'),
      meta: {
        requireAuth: true,
        title: '学生管理'
      }
    },
    {
      path: '/archivesManagement',
      name: 'archivesManagement',
      component: () => import('@/views/archivesManagement/archivesManagement'),
      meta: {
        requireAuth: true,
        title: '在校档案管理'
      }
    },
    {
      path: '/viewFile',
      name: 'viewFile',
      component: () => import('@/views/archivesManagement/viewFile'),
      meta: {
        requireAuth: true,
        title: '查看档案'
      }
    },
    {
      path: '/schoolPerformance',
      name: 'schoolPerformance',
      component: () => import('@/views/schoolPerformance/schoolPerformance'),
      meta: {
        requireAuth: true,
        title: '学生在校表现'
      }
    },
    {
      path: '/studentFile',
      name: 'studentFile',
      component: () => import('@/views/schoolPerformance/studentFile'),
      meta: {
        requireAuth: true,
        title: '学生档案'
      }
    },
    {
      path: '/payManagement',
      name: 'payManagement',
      component: () => import('@/views/payManagement/payManagement'),
      meta: {
        requireAuth: true,
        title: '缴费管理（班主任）'
      }
    },
    {
      path: '/paymentSetting',
      name: 'paymentSetting',
      component: () => import('@/views/payManagement/paymentSetting'),
      meta: {
        requireAuth: true,
        title: '缴费管理（领导）'
      }
    },
    {
      path: '/psychologicalCounselingLeader',
      name: 'psychologicalCounselingLeader',
      component: () => import('@/views/psychologicalCounseling/psychologicalCounselingLeader'),
      meta: {
        requireAuth: true,
        title: '心理咨询管理(领导)'
      }
    },
      {
        path:'/psychologicalCounseling',
        name:'psychologicalCounseling',
        component:()=>import('@/views/psychologicalCounseling/counseling'),
        meta:{
          requireAuth:true,
          title:'心理咨询室'
        }
      },
      {
        path:'/psychologicalCounselingList',
        name:'psychologicalCounselingList',
        component:()=>import('@/views/psychologicalCounseling/list'),
        meta:{
          requireAuth:true,
          title:'咨询信息'
        }
      },
    ]
  },
  {
    path: '/',
    name: 'login',
    component: login
  },
  {
    path: '/password',
    name: 'password',
    component: () =>
      import('@/views/password')
  },
  {
    path: '/register',
    name: 'register',
    component: () =>
      import('@/views/register')
  }
  ]
})



